body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logo__image {
  width: 100%;
  height: 100%;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.main {
  justify-content: center;
  width: 100%;
  display: flex;
}

.main__catalog {
  grid-template-rows: auto;
  align-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width <= 487px) {
  .main__catalog {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (width >= 488px) and (width <= 767px) {
  .main__catalog {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (width >= 768px) and (width <= 1023px) {
  .main__catalog {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (width >= 1024px) {
  .main__catalog {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 166px;
  max-width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s linear;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__price {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-top: 24px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.card__price-amount {
  color: #060b35;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
}

.card__name > p {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__name > p:hover {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__stars {
  color: #060b35;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  font-size: 10px;
  display: flex;
}

.card__stars .stars {
  display: flex;
}

.card__stars .stars .stars__star {
  background: url("star.822d561e.svg") 50% no-repeat;
  flex-shrink: 0;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__stars .stars .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
